







import { Vue, Component, Prop } from 'vue-property-decorator';

@Component
export default class CustomAccountButton extends Vue {
  public name: string = 'CustomAccountButton';

  @Prop() public sectionId!: string;
  @Prop() public sectionName!: string;
  @Prop() public url!: string;
  @Prop() public backgroundColor?: string;
  @Prop() public textColor?: string;
  @Prop() public textColorTheme?: string;

  get computedTextColor(): string {
    return this.textColor ? this.textColor + '--text' : '';
  }

  get computedTextColorTheme(): string {
    return this.textColorTheme ? 'text--' + this.textColorTheme : '';
  }
}
