










import { Vue, Component } from 'vue-property-decorator';
import CustomSection from '@/components/CustomSection.vue';
import CustomQiitaPost from '@/components/CustomQiitaPost.vue';
import axios from 'axios';

@Component({
  components: {
    CustomSection,
    CustomQiitaPost,
  },
})
export default class QiitaPosts extends Vue {
  public name: string = 'QiitaPosts';
  private responsedPosts: any[] = [];

  private created(): void {
    this.getPosts();
  }

  private getPosts(): void {
    axios.get('https://qiita.com/api/v2/users/tokoroten-lab/items?page=1&per_page=10')
      .then((response) => {
        this.responsedPosts = response.data;
      });
  }

  private getFormattedDateString(date: Date): string {
    const ymd: string = date.getFullYear() + '/' + date.getMonth() + '/' + date.getDay();
    const hms: string = date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
    return ymd + ' ' + hms;
  }

  get posts(): any[] {
    return this.responsedPosts.map((post) => {
      return {
        id: post.id,
        url: post.url,
        title: post.title,
        updated_at: this.getFormattedDateString(new Date(post.updated_at)),
        likes_count: post.likes_count,
        comments_count: post.comments_count,
      };
    });
  }

}
